<template>

    <el-menu
        class="common-side-bar"
        :default-active="$store.state.routerName"
        background-color="#304056"
        text-color="#BFCBD9"
        active-text-color="#318DF1"
        @select="select"

    >
        <!-- <el-menu-item index="ProjectList">
            <i class="iconfont" >&#xe631;</i>&nbsp;&nbsp;  
        </el-menu-item> -->
            <!-- <el-menu-item v-for="item of side_menu" :index="item.url" :key="item.url" :disabled="$store.state.routerName === 'ProjectList'">
                <span class="iconfont" v-html="item.code"></span>&nbsp;&nbsp;{{item.name}}
            </el-menu-item>  -->

          <template v-for="item in side_menu" >
                <template v-if="item.subs">
                    <el-submenu :index="item.index" :key="item.index" :disabled="$store.state.routerName === 'ProjectList'">
                        <template slot="title">
                            <span :class="item.code"  slot="title"></span>&nbsp;&nbsp;{{ item.title }}
                        </template>
                        <template v-for="subItem in item.subs" :disabled="$store.state.routerName === 'ProjectList'">
                            <el-submenu
                                v-if="subItem.subs"
                                :index="subItem.index"
                                :key="subItem.index">
                                <template slot="title">{{ subItem.title }}</template>
                                <el-menu-item
                                    v-for="(threeItem,i) in subItem.subs"
                                    :key="i"
                                    :index="threeItem.index"
                                >{{ threeItem.title }}</el-menu-item>
                            </el-submenu>
                            <el-menu-item
                                v-else
                                :index="subItem.index"
                                :key="subItem.index">
                                {{ subItem.title }}
                            </el-menu-item>
                        </template>
                    </el-submenu>
                </template>
                <template v-else-if="item.title==''">
                    <el-menu-item :index="item.index" :key="item.index" :disabled="$store.state.routerName === 'ProjectList'">
                        <template slot="title">
                            <span :class="item.code"  slot="title"></span>&nbsp;&nbsp;{{ item.title }}
                        </template>
                    </el-menu-item>
                </template>
                <template v-else>
                    <el-menu-item :index="item.index" :key="item.index">
                        <template slot="title">
                            <span :class="item.code"  slot="title"></span>&nbsp;&nbsp;{{ item.title }}
                        </template>
                    </el-menu-item>
                </template>
            </template>
    </el-menu>
</template>

<script>
import bus from '../components/bus';
    export default {
        name: "Side",
        data() {
            return {
                side_menu: [
                    {
                        code:'el-icon-s-home',
                        title:'  ',
                        index:'ProjectList',
                    },
                    {
                        title: "",
                        index: "ProjectDetail",
                        code: "el-icon-collection",
                    },
                    {
                        code: 'el-icon-menu',
                        index: 'api',
                        title: 'API',
                        subs:[
                            // {name: "", url: "ProjectDetail", code: "&#xe64a;"},
                            // {name: "", url: "DataBase", code: "&#xe63c;"},
                            {title: "API ", index: "RecordApi",},
                            {title: "", index: "AutoTest", },
                            {title: "", index: "RecordConfig",},
                            {title: "", index: "GlobalEnv", },
                            {title: "", index: "HostIP", },
                            {title: "", index: "DebugTalk", },
                            {title: "", index: "Task",},
                            {title: "", index: "Reports", },
                            {title: "", index: "TaskMeta", },
                        ]
                    },
                    {
                        code: 'el-icon-s-order',
                        index: 'webselemium',
                        title: 'web',
                        subs: [
                            {
                                index: 'webtestcaselist',
                                title: 'UI'
                            },
                            {
                                index: 'UICaseTestPlan',
                                title: 'UI'
                            },
                            {
                                index: 'UIReportList',
                                title: 'UI'
                            },
                        ]

                    },
                ],
            }
        },

    created() {
        //  Event Bus 
        bus.$on('collapse', msg => {
            this.collapse = msg;
            bus.$emit('collapse-content', msg);
        });
    },
        methods:{
            select(url) {
                this.$store.commit('setRouterName',url);
                this.$router.push({name:url});
                this.setLocalValue("routerName",url);
            }
        }
    }
</script>

<style scoped>

    .common-side-bar {
        position: fixed;
        top: 48px;
        border-right: 1px solid #ddd;
        height: 100%;
        width: 202px;
        background-color: #fff;
        display: inline-block;
    }
</style>



